// Styles for both the Editor and the front end.
.wp-block-outermost-mega-menu {
	overflow: hidden;
	position: relative;

	.wp-block-outermost-mega-menu__toggle {
		background-color: initial;
		border: none;
		color: currentColor;
		cursor: pointer;
		font-family: inherit;
		font-size: inherit;
		font-style: inherit;
		font-weight: inherit;
		line-height: inherit;
		padding: 0;
		text-align: left;
		text-transform: inherit;

		.wp-block-outermost-mega-menu__toggle-icon {
			align-self: center;
			display: inline-block;
			font-size: inherit;
			height: 0.6em;
			line-height: 0;
			margin-left: 0.25em;
			padding: 0;
			width: 0.6em;

			svg {
				stroke: currentColor;
				display: inline-block;
				height: inherit;
				margin-top: 0.075em;
				width: inherit;
				transition: all 0.1s linear;
				transform: rotate(-90deg);
			}
		}

		&[aria-expanded="true"] {
			.wp-block-outermost-mega-menu__toggle-icon {
				svg {
					transform: rotate(0deg);
				}
			}

			& ~ .wp-block-outermost-mega-menu__menu-container {
				opacity: 1;
				overflow: visible;
				visibility: visible;
			}
		}
	}
}
